head {
    background-color: lightseagreen
}
body {
    
}

.container {
    border: 1px solid green;
}

.main-container {
    min-height: 100vh; /* will cover the 100% of viewport */
    overflow: hidden;
    display: block;
    position: relative;
    padding-bottom: 100px; /* height of your footer */
   }

.row {
    text-align: center;
    height: 300;
}

.footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
}

.main-table {
    vertical-align: middle;
    color: rgb(18, 156, 0);
}

.hello {
    padding: 40px;
    font-family: Arial;
    text-align: center;
}

.filter-unit {
    padding-top: 10%;
}

.filter-bar {
    display: block;
    width: 100%;
    background-color: green;
}

.search-button-col {
    padding: 10%;
}

.bTable {
    cursor: pointer;
}

.filter-by {
    text-align: left;
    color: red;
    padding: 0.5em;
}

.major-line {
    border: 2px solid black;
}

.minor-line {
    border: 0.5px dashed black;
}

.feature-buttons {
    color: black;
    width: 100%;
    border: 1px solid;
    background: #efefef;
    padding: 10px 2px;
    cursor: pointer;
    transition: 0.4s;
}

.feature-buttons:hover {
    border: 1px solid dodgerblue;
    background-color: dodgerblue;
    color: white;
}

.feature-buttons:active {
    background-color: darkblue;
    transform: translateY(4px); 
}

.more-button {
    cursor: pointer;
    transition: 0.4s;
    background: #efefef;
}

.more-button:hover {
    border: 1px solid dodgerblue;
    background-color: dodgerblue;
    color: white;
}

.more-button:active {
    background-color: darkblue;
    transform: translateY(4px); 
}

.delete-buttons {
    color: black;
    width: 100%;
    border: 1px solid;
    background: #efefef;
    padding: 10px 2px;
    cursor: pointer;
    transition: 0.4s;
}

.delete-buttons:hover {
    border: 1px solid #ff3e30;
    background-color: #ff3e30;
    color: white;
}

.delete-buttons:active {
    background-color: #cf1002;
    transform: translateY(4px); 
}

.last-line {
    border: 0.5px solid;
    text-align: center;
    width: auto;
}
